.settings_page_auto-copy-posts-wordpress #screen-meta-links, .settings_page_auto-copy-posts-wordpress .update-nag {
  display: none;
}

.auto-copy-posts-for-wordpress {
  margin-top: 0;
}

.auto-copy-posts-for-wordpress .header {
  color: #fff;
  background: #2c2c54;
  grid-template-columns: 1fr;
  width: 100%;
  margin-top: -10px;
  margin-left: -22px;
  padding: 30px 20px;
  display: grid;
}

.auto-copy-posts-for-wordpress .header h2 {
  color: #fff;
}

.auto-copy-posts-for-wordpress .header .notice {
  color: #000;
  grid-row: 1;
}

.auto-copy-posts-for-wordpress .actions {
  background: #d1ccc0;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin-bottom: 40px;
  margin-left: -22px;
  padding: 15px 20px;
  display: flex;
}

.auto-copy-posts-for-wordpress .actions a {
  margin-right: 20px;
}

.auto-copy-posts-for-wordpress #settings-form .form-table tbody {
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.auto-copy-posts-for-wordpress #settings-form .form-table tbody tr {
  grid-column: span 2;
}

.auto-copy-posts-for-wordpress #settings-form .form-table tbody tr:nth-child(n+9) {
  grid-column: inherit;
}

.auto-copy-posts-for-wordpress #settings-form .form-table tbody tr:nth-child(8) {
  border-bottom: 1px solid #2c2c54;
}
